.cover
	width: 100%
	height: 100dvh
	position: relative

	&::before
		content: ''
		width: 100%
		height: 50dvh
		background: url("../img/cover-circuits.svg") no-repeat center top
			size: 100% auto
		position: absolute
		top: 0
		left: 0
		opacity: .3
		mix-blend-mode: hard-light

	.logo
		width: 50vw
		height: auto
		display: block
		position: absolute
		top: 50%
		left: 50%
		transform: translate(-50%, -50%)
		z-index: 3

		svg
			width: 100%
			height: auto

		// radial
		&::before
			content: ''
			width: 80vw
			height: 80vw
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%, -58%)
			background: radial-gradient(circle, rgb(255, 255, 255) 20%, rgba(255, 255, 255, 0) 80%)
			z-index: -1
			transition: opacity .5s $ease .2s

		.symbol
			transform: scale(.95)
			transition: opacity .5s $ease, transform .4s cubic-bezier(.76,-0.01,0,1.54)
			transform-origin: center
		.type
			transition: transform .7s cubic-bezier(.76,-0.01,0,1.54)
			transform-origin: center
			transform: scale(.92)

		&.active
			&::before
				opacity: .1
			.symbol
				opacity: .1
				// transform: scale(.85)
			.type
				transform: scale(.98)


	.marquee
		width: 100%
		height: 6rem
		overflow: hidden
		position: absolute
		top: 50%
		left: 0
		transform: translateY(-120%)

		strong
			display: block
			width: max-content
			height: 100%
			line-height: 6rem
			color: var(--brand)
			font-size: 5rem
			animation: marquee 10s linear infinite
			font-family: "Sixtyfour", sans-serif
			font-optical-sizing: auto
			font-weight: 400
			font-variation-settings: "BLED" 0, "SCAN" 0

	@keyframes marquee
		from
			transform: translateX(0)
		to
			transform: translateX(-50.5%)

	& > footer
		width: 100%
		position: absolute
		bottom: 10%
		left: 0
		z-index: 4
		text-align: center

		h2
			font-size: 2rem
			font-weight: 200
			margin-bottom: 0.5rem


		small
			position: relative
			font-size: 1.25rem
			font-family: "Space Mono", monospace
			font-weight: 700

			&::before,
			&::after
				content: ''
				width: 1rem
				height: 1rem
				border: solid 1px var(--brand)
				border-radius: 100%
				position: absolute
				top: 50%
				transform: translateY(-50%)
			&::before
				left: -2rem
				animation: blink .5s linear infinite
			&::after
				right: -2rem
				animation: blink .5s linear infinite


@keyframes blink
	0%
		opacity: 1
	50%
		opacity: 1
	51%
		opacity: 0
	100%
		opacity: 0


@media (max-width: 64rem) and (orientation: landscape)
	.cover
		& > footer
			bottom: 12%


@media (max-width: 64rem) and (orientation: portrait)
	.cover
		&::before
			background-size: 150% auto
		.logo
			width: 80vw
		.marquee
			height: 7rem
			strong
				font-size: 7rem
				line-height: 7rem
		& > footer
			h2
				font-size: 4vw
				font-weight: 300
			small
				font-size: 3vw


@include for-size(tablet_md)
	.cover
		.marquee
			height: 10vw
			strong
				font-size: 10vw
				line-height: 10vw


@include for-size(phone)
	.cover
		&::before
			background-size: 250% auto
		.logo
			&::before
				width: 100vw
				height: 100vw
				background-image: radial-gradient(circle, rgb(255, 255, 255) 35%, rgba(255, 255, 255, 0) 80%)
			svg
				height: 70dvh
			.symbol
				transform: scale(2)
			.type
				transform: scale(0.92) translateY(20vw)
			&.active
				.type
					transform: scale(.98) translateY(20vw)
		.marquee
			height: 20vw
			strong
				font-size: 20vw
				line-height: 20vw
		& > footer
			bottom: 8%
			h2
				font-size: 8vw
				margin-bottom: 1.5rem
			small
				font-size: 5vw



