.marquee-security
	width: 100%
	height: 70dvh
	position: relative
	overflow: hidden
	box-sizing: border-box
	padding-top: 20%


	&::before
		content: ''
		width: 100%
		height: 100%
		background: url("../img/security-bg.svg") no-repeat center
			size: contain
		position: absolute
		top: 0
		left: 0
		mix-blend-mode: multiply
		z-index: 0


	&::after
		content: ''
		width: 480px
		height: 580px
		background: url("../img/locks.svg") no-repeat center
			size: contain
		position: absolute
		top: 50%
		left: 50%
		mix-blend-mode: difference
		z-index: 0
		// transform: translate(-50%, -50%)
		animation: pulse 2s alternate infinite


	@keyframes pulse
		from
			transform: translate(-50%, -50%) scale(1.0)
		to
			transform: translate(-50%, -50%) scale(.92)

	strong
		display: block
		width: max-content
		height: 6rem
		line-height: 6rem
		color: var(--brand)
		font-size: 5rem
		animation: marquee-security 8s linear infinite
		font-family: "Sixtyfour", sans-serif
		font-optical-sizing: auto
		font-weight: 400
		font-variation-settings: "BLED" 0, "SCAN" 0
		mix-blend-mode: multiply

@keyframes marquee-security
	from
		transform: translateX(0)
	to
		transform: translateX(-51%)

.security
	width: 100%
	display: flex
	justify-content: center
	gap: 2.5rem

	hgroup
		width: 36.25rem
		height: 22rem
		box-sizing: border-box
		border-radius: 1.25rem
		background-image: linear-gradient(to bottom, #FF150A, #FFA10A)
		padding: 0 5rem 0 1.25rem
		display: flex
		flex-direction: column
		justify-content: center
		gap: 1.25rem
		position: relative
		overflow: hidden
		mix-blend-mode: multiply

		&::after
			content: ''
			width: 26.5rem
			height: 26.5rem
			background: url("../img/orb.svg") no-repeat center
				size: contain
			position: absolute
			top: -10%
			right: -40%
			// transform: translateY(-50%)
			z-index: 0
			mix-blend-mode: plus-lighter
			animation: spin 12s linear infinite


		h3
			background-color: rgba(0,0,0,.3)
			border-radius: 1.25rem
			padding: .5rem 1.25rem
			position: relative
			z-index: 1
			font-size: 1.5rem
			font-family: "Space Mono", monospace
			font-weight: 700
			line-height: 2.25rem
			color: white
		em
			color: var(--neutral_90)
		p
			background-color: rgba(0,0,0,.3)
			border-radius: 1.25rem
			padding: .5rem 1.25rem
			position: relative
			z-index: 1
			font-size: 1.25rem
			font-family: "Space Mono", monospace
			font-weight: 500
			line-height: 1.75rem
			color: white
	h2
		width: 30rem
		height: 22rem
		box-sizing: border-box
		border-radius: 1.25rem
		padding: 0 2.5rem 0 5rem
		display: flex
		flex-direction: column
		justify-content: center
		position: relative
		font-size: 2.5rem
		line-height: 3.5rem
		font-weight: 800
		text-align: right
		overflow: hidden

		&::before
			content: ''
			width: 100%
			height: 100%
			background-color: #2D2D2D
			border-radius: 1.25rem
			position: absolute
			top: 0
			left: 0
			mix-blend-mode: difference
			z-index: -1

		&::after
			content: ''
			width: 26.5rem
			height: 26.5rem
			background: url("../img/orb.svg") no-repeat center
				size: contain
			position: absolute
			top: -10%
			left: -50%
			// transform: translateY(-50%)
			z-index: 0
			mix-blend-mode: color-dodge
			animation: spin 12s linear infinite



@include for-size(tablet_lg)
	.marquee-security
		height: 40rem
		padding-top: 25%

	.security
		width: 90vw
		margin: 0 auto
		flex-wrap: wrap
		hgroup
			flex: 0 0 100%
			padding: 4vw
			height: auto
			text-align: center
			&::after
				right: initial
				left: 0
				top: 0
			h3
				font-size: 3vw
				line-height: 3.5vw
			p
				font-size: 2.7vw
				line-height: 3vw
		h2
			flex: 0 0 100%
			height: auto
			text-align: center
			font-size: 5vw
			line-height: 6.5vw
			padding: 10vw 4vw
			&::after
				left: 0
				top: -90%



@include for-size(tablet_md)
	.marquee-security
		padding-top: 35%



@include for-size(phone)
	.marquee-security
		height: 25rem
		&::before
			background-size: cover
		&::after
			width: 100%
			height: 100%

	.security
		margin: 2rem auto 0
		gap: .5rem
		hgroup
			border-radius: 2rem
			&::after
				width: 100%
				height: 100%
				top: initial
				bottom: -50%
				background-size: 100% auto
			h3
				font-size: 5vw
				line-height: 6vw
			p
				font-size: 5vw
				line-height: 6vw
		h2
			font-size: 7vw
			line-height: 7.5vw
			&::after
				width: 100%
				height: 100%
				top: -50%
				background-size: 100% auto
