.menu
	display: flex
	align-items: center
	gap: .75rem
	position: fixed
	top: 2rem
	left: 50%
	transform: translateX(-50%)
	z-index: 10
	transition: top 1s $ease .5s

	&.expanded
		top: .75rem
		.logo
			img
				width: 3.25rem
				opacity: 1
	.logo
		flex: 0 0 auto
		height: 3.25rem
		display: flex
		align-items: center
		img
			width: 0
			height: auto
			opacity: .3
			transition: width .3s $ease, opacity .5s, transform .2s

			&:hover
				transform: scale(1.05)

	ul
		border: solid 1px var(--opacity_20)
		border-radius: .75rem
		display: flex
		align-items: center
		font-size: .875rem
		text-transform: uppercase
		box-sizing: border-box
		letter-spacing: 1px
		box-shadow: 0 .3rem .75rem rgba(0, 0, 0, .2)

		&:first-of-type
			height: 2.5rem
			padding: 0 .75rem
			gap: 1.25rem
			background-image: $gradient
			li
				height: 100%
				position: relative
				&:not(:first-child)
					&::before
						content: ''
						width: 1px
						height: 1.1rem
						background-color: var(--opacity_20)
						position: absolute
						top: 50%
						left: -.7rem
						transform: translateY(-50%)
					a
						padding: 0 .75rem
						transition: align-items .4s $ease
						color: var(--neutral_0)
						
			i
				display: block
				width: 1.25rem
				height: 1.25rem
				background-color: var(--neutral_0)
				mask-image: url("../img/icons/home.svg")
				mask-size: contain
			a
				font-weight: 900
				height: 100%
				display: flex
				align-items: center

				&:hover
					opacity: .6

		// lang
		&:last-of-type
			padding: 2px
			background-color: #FE9706

			li
				height: 1.75rem
			a
				height: 100%
				width: 2.25rem
				display: flex
				align-items: center
				justify-content: center
				font-weight: 700
				border-radius: .625rem
				box-sizing: border-box
				color: var(--neutral_0)

				&:hover
					opacity: .6

			.active
				opacity: 1
				background-color: var(--neutral_0)
				border: solid 1px var(--neutral_90)
				color: var(--neutral_90)

				&:hover
					opacity: 1
					cursor: default


@include for-size(phone)
	.menu
		gap: .5rem
		transition: margin .3s $ease
		ul
			&:first-of-type
				li:first-child
					display: none
				li
					&:not(:first-child)
						a
							padding: 0 .25rem



