.contact
	width: 100%
	display: flex
	justify-content: center
	align-items: flex-start
	gap: 2rem
	padding: 9rem 0
	position: relative
	background: url("../img/contact-arrows.svg") no-repeat center

	&::before
		content: ''
		width: 100%
		height: 150%
		background: url("../img/contact-bg.png")no-repeat center top
			size: cover
		position: absolute
		top: 0
		left: 0
		mix-blend-mode: exclusion

	hgroup
		flex: 0 0 26.25rem
		align-self: stretch
		box-sizing: border-box
		padding: 11.25rem 2.5rem 4.5rem
		background-image: $gradient
		border-radius: 3rem
		position: relative
		overflow: hidden
		mix-blend-mode: multiply
		z-index: 1

		&::before
			content: ''
			width: 100%
			height: 100%
			background: url("../img/circuit.svg") no-repeat 63% 120%
				size: 170% auto
			position: absolute
			top: -4rem
			left: 2rem
			opacity: .7
			mix-blend-mode: overlay
			transform: rotate(160deg)

		&::after
			content: ''
			width: 8.75rem
			height: 8.75rem
			background: url("../img/icons/postal.svg") no-repeat 0 0
				size: contain
			position: absolute
			top: 1.75rem
			left: 2.5rem
			z-index: 1

		h2
			font-size: 2.5rem
			font-weight: 800
			line-height: 2.75rem
			color: var(--neutral_0)
			position: relative
			z-index: 1
		p
			font-size: 1.5rem
			font-family: "Space Mono", monospace
			font-weight: 500
			line-height: 1.75rem
			color: white
			margin-top: 2rem
			position: relative
			z-index: 1

	& > div
		flex: 0 0 26.25rem
		align-self: stretch
		box-sizing: border-box
		padding: .625rem
		position: relative
		display: flex
		flex-direction: column
		gap: .25rem
		// margin-top: 12rem
		z-index: 1

		&::before
			content: ''
			width: 100%
			height: 100%
			background-color: #26476a
			mix-blend-mode: hard-light
			border-radius: 1.75rem
			position: absolute
			top: 0
			left: 0
			z-index: -1

	input, textarea
		width: 100%
		height: 4rem
		border-radius: 1.25rem
		color: var(--neutral_0)
		font-family: "Outfit", sans-serif
		font-weight: 800
		border: none
		outline: none
		box-sizing: border-box
		padding: 0 1rem 0 2.5rem
		background:
			color: var(--neutral_90)
			repeat: no-repeat
			position: .75rem 1.5rem
			size: 1rem 1rem

		&::placeholder
			color: var(--neutral_0)
		&:hover
			outline: solid 1px #ffffff52
		&:focus
			outline: solid 1px var(--brand)

	textarea
		flex: 0 0 15rem
		padding: 1.25rem 1rem 1rem 2.5rem
		background-image: url("../img/icons/comment.svg")

	fieldset
		display: flex
		gap: .25rem

	input[name="name"]
		background-image: url("../img/icons/user.svg")
	input[type="email"]
		background-image: url("../img/icons/mail.svg")
	input[type="tel"]
		background-image: url("../img/icons/smartphone.svg")
	
	input[type="submit"]
		border: solid 1px var(--neutral_0)
		height: 7.5rem
		cursor: pointer
		margin-top: 1.25rem
		background:
			image: url("../img/icons/arrow.svg"), url("../img/circuit.svg")
			repeat: no-repeat
			position: 90% center, 60% 70%
			color: transparent
			size: 4rem auto, 170% auto
		text-align: left
		font-size: 1.5rem
		transition: background-position .5s $ease

		&:hover
			background-position: 97% center, 60% 65%



@include for-size(tablet_lg)
	.contact
		width: 90vw
		margin: 0 auto
		hgroup
			flex: 1 1 50%
		& > div
			flex: 1 1 50%


@include for-size(tablet_md)
	.contact
		gap: .5rem
		hgroup
			h2
				font-size: 4vw
				line-height: 5vw
		textarea
			flex: 1


@include for-size(phone)
	.contact
		flex-wrap: wrap
		padding: 5rem 0
		hgroup
			flex: 0 0 100%
			text-align: center
			padding: 11.25rem 2.5rem 2.5rem
			&::after
				left: 50%
				transform: translateX(-50%)
			h2
				font-size: 7vw
				line-height: 8vw
		& > div
			align-self: initial
		fieldset
			flex-wrap: wrap
			input
				flex: 0 0 100%
		textarea
			flex: 0 0 12rem


