.about

	div
		width: 90%
		max-width: $main_column
		height: 30rem
		margin: 0 auto
		box-sizing: border-box
		padding: 0 17.5rem 0 7.5rem
		position: relative
		display: flex
		flex-flow: column nowrap
		justify-content: center
		gap: 2.5rem
		overflow: hidden
		transform: translateY(200px)
		opacity: 0

		&::before
			content: ''
			width: 100%
			height: 100%
			background: url("../img/circuit.svg") no-repeat left top
				size: contain
			position: absolute
			top: 0
			left: 0
		
		&:first-of-type
			color: #001018
			background-image: linear-gradient(25deg, #17eaff, #0559fe)
			mix-blend-mode: difference
			border-radius: 2.5rem 2.5rem 7rem .75rem
			&::before
				transform: rotate(90deg) translate(20%, 45%) scale(1.4)
				opacity: .5
				mix-blend-mode: saturation

			.about-star
				bottom: -15rem
			.about-cube
				bottom: -10.5rem
				

		&:last-of-type
			height: 26rem
			color: #040b1e
			background-color: #A3A39A
			mix-blend-mode: difference
			border-radius: 7rem .75rem 2.5rem 2.5rem
			&::before
				opacity: .5
				mix-blend-mode: soft-light

			.about-star
				top: -15rem
			.about-cube
				top: -10.5rem
			
	h2
		font-size: 3.7rem
		font-weight: 800
		line-height: 1.1
		position: relative
		z-index: 2
	p
		font-size: 1.75rem
		font-family: "Space Mono", monospace
		font-weight: 700
		line-height: 1.1
		position: relative
		z-index: 2



	.about-star
		width: 30rem
		height: 30rem
		position: absolute
		right: -3.5rem
		background: url("../img/about-star.svg") no-repeat center
			size: 100% auto
		z-index: 1
		// animation: spin 10s linear infinite
	.about-cube
		width: 21rem
		height: 21rem
		position: absolute
		right: 1rem
		background: url("../img/about-cube.svg") no-repeat center
			size: 100% auto
		z-index: 3
	.about-circle
		width: 17.5rem
		height: 17.5rem
		position: absolute
		top: 2rem
		right: 3rem
		background: url("../img/about-circle.svg") no-repeat center
			size: 100% auto
	.about-btc
		width: 7.5rem
		height: 7.5rem
		position: absolute
		bottom: 15.5rem
		right: 8rem
		background: url("../img/about-btc.svg") no-repeat center
			size: 100% auto
		z-index: 2
	.about-coins
		width: 16rem
		height: 5rem
		position: absolute
		bottom: -1.2rem
		right: 2rem
		background: url("../img/about-coins.svg") no-repeat center
			size: 100% auto
		z-index: 2
	.about-coin1
		width: 6rem
		height: 5.75rem
		position: absolute
		top: 0
		right: 8rem
		background: url("../img/about-coin1.svg") no-repeat center
			size: 100% auto
		z-index: 1
	.about-coin2
		width: 4rem
		height: 4rem
		position: absolute
		top: -.5rem
		right: 6rem
		background: url("../img/about-coin2.svg") no-repeat center
			size: 100% auto
		z-index: 1
	.about-coin3
		width: 5.5rem
		height: 6rem
		position: absolute
		top: -5rem
		right: 13rem
		background: url("../img/about-coin3.svg") no-repeat center
			size: 100% auto
		z-index: 1
	.about-coin4
		width: 5.5rem
		height: 5.5rem
		position: absolute
		top: -10rem
		right: 8rem
		background: url("../img/about-coin4.svg") no-repeat center
			size: 100% auto
		z-index: 1
	.about-coin5
		width: 4rem
		height: 3rem
		position: absolute
		top: -10rem
		right: 4rem
		background: url("../img/about-coin5.svg") no-repeat center
			size: 100% auto
		z-index: 1
	.about-coin6
		width: 5rem
		height: 5rem
		position: absolute
		top: -10rem
		right: 12rem
		background: url("../img/about-coin6.svg") no-repeat center
			size: 100% auto
		z-index: 1
	.about-coin7
		width: 5.5rem
		height: 3rem
		position: absolute
		top: -10rem
		right: 7rem
		background: url("../img/about-coin7.svg") no-repeat center
			size: 100% auto
		z-index: 1




@keyframes spin
	0%
		transform: rotate(0deg)
	25%
		transform: rotate(90deg)
	50%
		transform: rotate(180deg)
	75%
		transform: rotate(275deg)
	100%
		transform: rotate(359deg)



@include for-size(tablet_lg)
	.about
		div
			padding: 0 17.5rem 0 3.5rem


@include for-size(tablet_md)
	.about
		div
			h2
				font-size: 5vw


@include for-size(phone)
	.about
		div
			height: auto
			text-align: center
			&:first-of-type
				padding: 2rem 6vw 30rem 6vw
				&::before
					height: 60vw
					transform: rotate(180deg) scale(1.5)
			&:last-of-type
				height: auto
				padding: 30rem 6vw 4rem 6vw
				&::before
					height: 60vw
					transform: rotate(180deg) scale(1.5)
			h2
				font-size: 9vw
		p
			font-size: 5vw

		.about-circle
			top: initial
			bottom: 10.5rem
			right: 50%
			transform: translateX(50%)
		.about-btc
			right: 33%
		.about-coins
			bottom: 82vw
		.about-cube
			right: 50%
			transform: translateX(50%)


