.gateway
	width: 100%
	height: 100dvh
	position: relative
	display: flex
	align-items: center
	justify-content: center

	p
		width: 90%
		max-width: $main_column
		font-weight: 900
		font-size: 5rem
		position: relative

		span:first-child
			background: linear-gradient(10deg, #ff7e03, #9e1008)
			-webkit-background-clip: text
			-webkit-text-fill-color: transparent
			mix-blend-mode: hard-light
			position: relative
			z-index: 1

		span:last-child
			color: #6d1f11
			mix-blend-mode: color-burn
			position: absolute
			top: 1rem
			left: 0

	.text > div
		// background: linear-gradient(to right, rgb(0, 0, 0) 50%, rgb(37, 37, 37) 50%)
		background: linear-gradient(to right, ($brand-red, $brand-yellow) 75%, rgb(125, 125, 125) 25%)
		background-size: 200% 100%
		background-position-x: 100%
		color: transparent
		background-clip: text
		-webkit-background-clip: text
		margin-left: 50px


	.coin
		width: 5rem
		height: 5rem
		position: absolute
		z-index: 2
		transition: transform .3s $ease
		animation-name: floating
		animation-direction: alternate
		animation-iteration-count: infinite
		// mix-blend-mode: multiply
		opacity: 0
		margin-top: 5rem

		&:hover
			transform: scale(0.8)
		
		span
			display: block
			width: inherit
			height: inherit
			background-size: contain
			background-repeat: no-repeat
			mix-blend-mode: multiply
			transition: transform .15s

		$coin-list: bitcoin ethereum binance cardano dogecoin litecoin polkadot tron solana tether usdcoin terra_classic xrp avalanche
		@each $coin in $coin-list
			&.#{$coin}
				// $randomX: percentage(random(80) / 100)
				// $randomY: percentage(random(100) / 100)
				// top: $randomY
				// left: $randomX
				// $randomDuration: random(4)
				// $randomDelay: random(5)
				// animation: floating $randomDuration + s $randomDelay + s alternate infinite
				span
					background-image: url("../img/coins/#{$coin}.svg")

		@keyframes floating
			from
				transform: translateY(0)
			to
				transform: translateY(4rem)

		&.bitcoin
			top: 8%
			left: 4%
			animation-duration: 3s
			animation-delay: 1s
		&.ethereum
			top: -16%
			left: 28%
			animation-duration: 2.5s
			animation-delay: .2s
		&.polkadot
			top: 16%
			left: 36%
			animation-duration: 2.5s
			animation-delay: .4s
		&.usdcoin
			top: 11%
			left: 46%
			animation-duration: 3.5s
			animation-delay: .2s
		&.xrp
			top: -19%
			left: 71%
			animation-duration: 3.2s
			animation-delay: .1s
		&.tether
			top: 23%
			left: 60%
			animation-duration: 3.8s
			animation-delay: .8s
		&.litecoin
			top: 17%
			left: 85%
			animation-duration: 2.8s
			animation-delay: .1s
		&.avalanche
			top: 45%
			left: 91%
			animation-duration: 2.9s
			animation-delay: .4s
		&.cardano
			top: 22%
			left: 15%
			animation-duration: 3.2s
			animation-delay: .2s
		&.binance
			top: 65%
			left: 11%
			animation-duration: 2.7s
			animation-delay: .7s
		&.dogecoin
			top: 60%
			left: 44%
			animation-duration: 3.7s
			animation-delay: .1s
		&.tron
			top: 76%
			left: 64%
			animation-duration: 2.2s
			animation-delay: 1.3s
		&.terra_classic
			top: 79%
			left: 88%
			animation-duration: 3.2s
			animation-delay: 1.1s
		&.solana
			top: 82%
			left: 24%
			animation-duration: 3.9s
			animation-delay: 1.4s


@media (max-width: 64rem) and (orientation: portrait)
	.gateway
		height: 50dvh

@include for-size(tablet_lg)
	.gateway
		p
			font-size: 6.5vw
		.coin
			width: 3.5rem
			height: 3.5rem
		.coin.xrp
			top: 0


@include for-size(phone)
	.gateway
		p
			font-size: 12vw
		.text > div
			margin-left: 0
			text-align: center !important

		.coin
			width: 3rem
			height: 3rem
			z-index: 0
			opacity: .2 !important



