.benefits
	width: 90%
	height: 100vh
	max-width: $main_column
	margin: 14rem auto
	display: flex
	gap: 2.5rem

	aside
		flex: 1
		i
			width: 5rem
			height: 5rem
			background-repeat: no-repeat
			position: relative
			z-index: 1
		h2
			font-size: 3rem
			line-height: 4rem
			font-weight: 800
			position: relative
			z-index: 1

		em
			background-color: var(--neutral_90)
			font-size: 1.25rem
			text-transform: uppercase
			font-weight: 400
			height: 3rem
			line-height: 3rem
			border-radius: 1.5rem
			padding: 0 1rem
			display: inline-block
		& > div
			padding: 2.5rem 1.25rem 2.5rem 2.5rem
			display: flex
			gap: 1.25rem
			border-radius: 2.5rem 4rem 2.5rem 2.5rem
			position: relative
			overflow: hidden
			margin-bottom: 2.25rem
			opacity: 0
			transform: translateY(400px)
		header
			flex: 1
			position: relative
			z-index: 1

		.ben1
			background-image: $gradient
			color: var(--neutral_0)
			// mix-blend-mode: multiply

			&::before
				content: ''
				width: 100%
				height: 100%
				background: url("../img/circuit.svg") no-repeat left bottom
					size: 170% auto
				position: absolute
				top: 0
				left: 0
				opacity: .7
				mix-blend-mode: overlay
			h2
				margin-bottom: 3.5rem
			i
				background-image: url("../img/icons/chip.svg")

		.ben2
			outline: solid 1px $brand-red
			background-color: var(--neutral_0)
			// mix-blend-mode: multiply

			&::before
				content: ''
				width: 100%
				height: 100%
				mask-image: url("../img/circuit.svg")
				mask-repeat: no-repeat
				mask-position: left 50%
				mask-size: 170% auto
				background-image: $gradient
				position: absolute
				top: 0
				left: 0
				opacity: .25
			h2
				margin-bottom: 3.5rem
				background: linear-gradient(-180deg, #ff9103, #f00d00)
				-webkit-background-clip: text
				-webkit-text-fill-color: transparent
			i
				background-image: url("../img/icons/digital-wallet.svg")
			em
				color: var(--neutral_0)

		.ben3
			outline: solid 1px $brand-red
			background-color: var(--neutral_0)
			padding: 1rem 1.25rem 1rem 2.5rem
			transform: translateY(200px)
			// mix-blend-mode: multiply

			&::before
				content: ''
				width: 100%
				height: 100%
				mask-image: url("../img/circuit.svg")
				mask-repeat: no-repeat
				mask-position: left 30%
				mask-size: 170% auto
				background-image: $gradient
				position: absolute
				top: 0
				left: 0
				opacity: .25
			h2
				color: $brand-red
				flex: 1
			i
				background-image: url("../img/icons/cryptocurrency.svg")

		.ben4
			background-color: #E5E5E5
			padding: 1rem 1.25rem 1rem 2.5rem
			transform: translateY(300px)
			// mix-blend-mode: multiply

			&::before
				content: ''
				width: 100%
				height: 100%
				mask-image: url("../img/circuit.svg")
				mask-repeat: no-repeat
				mask-position: left 80%
				mask-size: 170% auto
				background-image: $gradient
				position: absolute
				top: 0
				left: 0
				opacity: .25
			h2
				color: var(--neutral_90)
				font-size: 2.8rem
				flex: 1
			i
				background-image: url("../img/icons/btc-coins.svg")

		.ben5, .ben6
			background-color: var(--neutral_90)
			padding: 1rem 1.25rem 1rem 2.5rem
			transform: translateY(400px)
			// mix-blend-mode: multiply

			&::before
				content: ''
				width: 100%
				height: 100%
				mask-image: url("../img/circuit.svg")
				mask-repeat: no-repeat
				mask-position: left 80%
				mask-size: 170% auto
				background-image: $gradient
				position: absolute
				top: 0
				left: 0
				opacity: .6
				mix-blend-mode: luminosity
			h2
				color: #C9D2DA
				font-size: 2.8rem
				flex: 1
			i
				background-image: url("../img/icons/convert.svg")
		.ben6
			i
				background-image: url("../img/icons/shine-coins.svg")


	.col-1
		padding-top: 40rem
		box-sizing: border-box
		// .crypto-card
		// 	&:nth-child(1)
		// 		transform: translateY(50rem)
			// &:nth-child(2)
			// 	opacity: 1
	.col-2
		height: 100%
		overflow: hidden
		& > div
			display: block
			transform: translateY(-110%)

	.crypto-card
		width: 13.125rem
		box-sizing: border-box
		padding: 1.25rem
		background-image: linear-gradient(#237CA9, #0E3143)
		border-radius: 7rem
		color: var(--neutral_0)
		text-align: center
		// mix-blend-mode: multiply
		margin-bottom: 2.25rem
		// opacity: .01
		position: relative
		outline: solid .3rem var(--neutral_0)

		img
			width: 100%
			height: auto
		h3
			font-size: 2rem
			line-height: 5rem
			font-weight: 900
		strong
			font-size: 2rem
			line-height: 2.5rem
			font-weight: 800
			display: block
		small
			font-size: 1.25rem
			line-height: 1.5rem
			font-weight: 700
			display: block
		footer
			padding: 1rem 0 2.5rem
			display: flex
			align-items: center
			justify-content: center
			gap: .5rem
			font-family: "Space Mono", monospace
			font-weight: bold
		em
			padding: .25rem .25rem .25rem 1.5rem
			background-color: var(--neutral_90)
			border-radius: .5rem
			position: relative

			// bull/bear arrow
			&::before
				content: ''
				width: 1rem
				height: 1rem
				mask:
					image: url("../img/icons/arrow-mkt.svg")
					repeat: no-repeat
					size: 1rem 1rem
					position: center
				position: absolute
				top: 50%
				left: .25rem
				transform: translateY(-50%)

		&.bull
			em
				color: $bull
				&::before
					background-color: $bull
		&.bear
			em
				color: $bear
				&::before
					background-color: $bear
					transform: translateY(-50%) rotate(180deg)




@include for-size(tablet_lg)
	.benefits
		height: auto
		flex-wrap: wrap
		margin: 10rem auto
		aside
			order: 3
			flex: 0 0 100%
			display: flex
			justify-content: space-between
			flex-wrap: wrap
			& > div
				opacity: 1
				transform: initial
				flex: 0 0 49%
				flex-direction: column
				box-sizing: border-box
				padding: 3vw
			span, h2
				order: 2
			i
				order: 1
			h2
				font-size: 5vw
				line-height: 6vw
			em
				font-size: 2.5vw
			.ben1
				h2
					margin-bottom: 3vw
			.ben2
				h2
					margin-bottom: 4vw
					margin-right: 4.5vw
			.ben3
				padding: 3vw
				transform: initial
			.ben4
				padding: 3vw
				transform: initial
				h2
					font-size: 4vw
					line-height: 6vw
			.ben5, .ben6
				padding: 3vw
				transform: initial
				h2
					font-size: 4vw
					line-height: 5vw

		.col-1
			order: 1
			flex: 0 0 100%
			height: auto
			display: flex
			gap: 2vw
			padding-top: 0
		.col-2
			order: 2
			flex: 0 0 100%
			height: auto
			overflow: initial
			& > div
				transform: initial
				width: max-content
				display: flex
				gap: 2vw

// @include for-size(tablet_md)
@include for-size(phone)
	.benefits
		position: relative

		&::before
			content: ''
			width: 100vw
			height: 100dvh
			background: url("../img/cover-circuits.svg") repeat-y center top
				size: auto 50%
			position: absolute
			top: 0
			left: -5%
			opacity: .3

		.col-1
			transform: translateX(110%)
		.col-2
			& > div
				transform: translateX(-110%)

		aside
			& > div
				flex: 0 0 100%
				margin-bottom: 1rem
				margin-top: 4rem
				opacity: 0
				padding: 3vw 5vw
			h2
				font-size: 7vw
				line-height: 8vw
			em
				font-size: 3.8vw
			.ben1
				h2
					margin-bottom: 5vw
			.ben2
				h2
					margin-bottom: 5vw
					margin-right: 0
			.ben3
				padding: 3vw 5vw
				transform: initial
			.ben4
				padding: 3vw 5vw
				transform: initial
				h2
					font-size: 7vw
					line-height: 8vw
			.ben5, .ben6
				padding: 3vw 5vw
				transform: initial
				h2
					font-size: 7vw
					line-height: 8vw

