.advantages
	width: 90%
	height: 20rem
	max-width: $main_column
	margin: 14rem auto
	display: flex
	justify-content: space-between

	& > div
		flex: 0 0 20rem
		height: 20rem
		position: relative
		box-sizing: border-box
		padding: 1.5rem

		&::before
			content: ''
			width: 100%
			height: 100%
			mask-image: url("../img/adv-shape.svg")
			mask-size: contain
			mask-repeat: no-repeat
			mask-position: center
			position: absolute
			top: 0
			left: 0
			mix-blend-mode: multiply

		header
			display: flex
			width: 100%
			height: 100%
			position: relative
			z-index: 1
			box-sizing: border-box
			padding: 2rem

			&::before
				content: ''
				width: 100%
				height: 100%
				mask-image: url("../img/adv-line.svg")
				mask-size: contain
				mask-repeat: no-repeat
				mask-position: center
				position: absolute
				top: 0
				left: 0

		h2
			font-size: 2.5rem
			font-weight: 800
		svg
			position: absolute
			z-index: -1


		&:first-of-type
			// margin-top: -7rem
			&::before
				background-color: #cdcdcd
			header
				&::before
					background-color: var(--neutral_90)
			svg
				right: 2rem
				bottom: 2rem

			.mini
				transform-origin: 50% 50%
				animation: stars 1s alternate infinite
			.big
				transform-origin: 50% 50%
				animation: stars 1s alternate infinite 1s

			@keyframes stars
				0%
					transform: scale(1)
				50%
					transform: scale(1)
				50.1%
					transform: scale(0.8)
				100%
					transform: scale(0.8)

		&:nth-of-type(2)
			// margin-top: 7rem
			&::before
				background-color: $brand-red
			header
				align-items: flex-end
				&::before
					background-color: var(--neutral_0)
			h2
				text-align: right
				color: var(--neutral_0)
			svg
				right: 2rem
				top: 2rem
			path
				&:nth-of-type(2)
					transform-origin: 50% 50%
					animation: rocket 1s alternate infinite

			@keyframes rocket
				0%
					transform: translateY(0) scale(.8)
				33%
					transform: translateY(0) scale(.8)
				33.1%
					transform: translateY(-.3rem) scale(.8)
				66%
					transform: translateY(-.3rem) scale(.8)
				66.1%
					transform: translateY(-.6rem) scale(.8)
				100%
					transform: translateY(-.6rem) scale(.8)

		&:nth-of-type(3)
			&::before
				background-color: #cdcdcd
			header
				align-items: flex-end
				&::before
					background-color: var(--neutral_90)
			h2
				text-align: right
			svg
				right: 2rem
				top: 2rem
			path:nth-of-type(2)
				transform-origin: 50% 64.5%
				animation: gear 1s alternate infinite
			@keyframes gear
				0%
					transform: rotate(0)
				50%
					transform: rotate(0)
				50.1%
					transform: rotate(45deg)
				100%
					transform: rotate(45deg)


// adjusts in english lang
.en
	.advantages
		& > div
			&:nth-of-type(2)
				&::before,
				header::before
					transform: scale(-1)
			&:nth-of-type(3)
				&::before,
				header::before
					transform: scale(-1)



@include for-size(tablet_md)
	.advantages
		height: 60dvh
		flex-wrap: wrap
		justify-content: center


@include for-size(phone)
	.advantages
		height: auto
		margin: 10rem auto




